#user-head,#user-background {
  width: 100%;
  border-radius: 50%;
}

label {
  margin-bottom: 0;
  cursor: pointer;
  margin-left: 25%;
  width: 50%;
}

#upload-head,#upload-background {
  display: none;
}

.user-center *{
  font-family: 'Monaco', 'Lucida Console', monospace;
}

.glyphicon{
  font-family: 'Glyphicons Halflings';
}

#records-board,#articles-board{
  .glyphicon-remove{
    display: none;
    cursor: pointer;
  }
}